<template>
    <div>
      
      <el-col :span="2"><p></p></el-col>
          <el-col :span="5"><img src=".\..\assets\2.png" alt=""></el-col>
      <el-col :span="17">
  <div v-for="item in lists" :key="item.cardTypeName">
    <el-col :span="19"> 
      <el-col :span="1"> 
   <span  @click="tiaozuan2(item.author.loginname)"><img :src="item.author.avatar_url" class="touxiang xiaoshou" alt="" ></span>
    </el-col> 
    <el-col :span="2"> 
     <span class="shuliang" v-html="item.reply_count"></span> 
     <span class="shuliang">/</span>
     <span class="shuliang" v-html="item.visit_count"></span>
     </el-col> 
     <span v-if="item.top" class="zhiding">置顶</span>
     <span v-else-if="item.good" class="zhiding">精华</span>
     <!-- 标题 -->
      <span v-html="item.title" class="biaoti xiaoshou" @click="tiaozuan1(item.id)" ></span>
     <hr>
     </el-col> 
     <el-col :span="5"> 
     <span  @click="tiaozuan2(item.author.loginname)">
         <img :src="item.author.avatar_url" class="touxiang1 xiaoshou" alt="">
      </span>
     <span v-html="item.create_at" class="shijian"></span>
     </el-col> 
  </div>
   <el-col :span=20>
          <!--分页 -->
 <el-pagination
    layout="prev, next"
    :page-size="15"
     @next-click="xia"
      @prev-click ="shang"
    >
  </el-pagination>
     </el-col>
  </el-col>
    </div>
</template>


<script>
export default{
name:'qb',
props:['name'],
data:function(){
  return{
    lists:[],
    //当前页数
    page:1,
    //每一页显示数量
    limit:15
  }
},
methods: {
    //点击标题进入内容
    tiaozuan1(id){
        this.$router.push('/topics/'+id)
        
    },
    //点击头像进入个人信息
     tiaozuan2(loginname){
        this.$router.push('/user/'+loginname)  
    },
    
      //分页
       shang(){   
         var app=this;     
        this.$http.get('/topics',{
       params:{
          tab:this.name,
          page:this.page,
           limit:this.limit
      }
     }).then(function(response){
        if(response.data.success){
          app.page=-1;
           app.lists=response.data.data;
        }
     });
      },
      xia(){
         var app=this;
     this.$http.get('/topics',{
       params:{
          tab:this.name,
          page:this.page,
           limit:this.limit
      }
     }).then(function(response){
        if(response.data.success){
           app.lists=response.data.data;
           app.page+=1;
        }
     });
       }
    },
    //ajax
beforeRouteEnter:function(to,from,next){
  //进入组件之前的回调操作d
  //to待进入的路由组件
  //from离开的路由组件
  //next执行下一步操作
  //执行ajax请求
 
  next(function(vm){
    //vm表示当前组件的实例对象
    vm.$http.get('/topics',{
      params:{
          tab:to.params.name,
          page:vm.page,
          limit:vm.limit
      }
    }).then(function(response){
      //response表示响应消息对象
      //response.data表示响应消息主体
      if(response.data.success){
        vm.lists=response.data.data;
      }
    });
  });
},
//动态路由切换时的导航守卫
beforeRouteUpdate:function(to,from,next){
  var app=this;
    //从新发送ajax请求
    this.$http.get('/topics',{
      params:{
          tab:to.params.name,
          page:this.page,
          limit:this.limit
      }
    }).then(function(response){
       if(response.data.success){
        app.lists=response.data.data;
       }
    });
    next();
}
};

</script>






<style >
.touxiang{
  width: 30px;
    height: 30px;  
}
.touxiang1{
  width: 25px;
    height: 25px; 
}
.zhiding{
  background: #80bd01;
    padding: 2px 4px;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -o-border-radius: 3px;
    color: #fff;
    font-size: 12px;
}
.shuliang{
    font-size: 10px;
    color: #b4b4b4;
}
.biaoti{
    max-width: 58%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    display: inline-block;
    vertical-align: middle;
    font-size: 16px;
    line-height: 30px;
}
.shijian{
   color: #b4b4b4;
    font-size: 13px;
}
.fenlei{
    background-color: #e5e5e5;
    color: #999;
    padding: 2px 4px;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -o-border-radius: 3px;
    font-size: 12px;
}
.xiaoshou{
  cursor:pointer
}

</style>


