import Vue from 'vue'
import Router from 'vue-router'
import Qb from '@/components/qb.vue'
import Topics from '@/components/topics.vue'
import User from '@/components/user.vue'
import Xxtz from '@/components/xxtz.vue'
import Fbht from '@/components/fbht.vue'

//全局注册路由
Vue.use(Router)
//定义路由信息对象
  const routes=[
    {
      path: '/',
      name:'',
      component: Qb,
      redirect:'/all' //路由重定向 ，游览器默认访问
    },
    {
      path: '/:name',
      name: '',
      component:Qb,
      props:true
    },
    {
      path: '/user/:loginname',
      name: 'user',
      component: User,
      props:true
    },
    {
      path: '/topics/:id',
      name: 'topics',
      component: Topics,
      props:true

    },
    {
      path: '/xxtz/:loginname',
      name: 'xxtz',
      component: Xxtz,
      props:true

    },
    {
      path: '/fbht/:loginname',
      name: 'fbht',
      component: Fbht,
      props:true

    },
    
  ];
  //创建路由管理器
  const router=new Router({
    routes
  });
  
  
    //对外输出
  export default router;


  


