<template>
  <div id="app" >
    
    <!-- 导航栏 -->
  <el-row>
  <el-menu   mode="horizontal"  
  background-color="#333333"
  text-color="#fff"
  active-text-color="#ffd04b"
  default-active="/all"
  router
  >
  
  <el-col :span="5" :offset="2"><img src=".\assets\1.png" alt="" @click="tiaozuan3"></el-col>
  <el-menu-item index="/all">全部</el-menu-item>
  <el-menu-item index="/good">精华</el-menu-item>
  <el-menu-item index="/ask">问答</el-menu-item>
  <el-menu-item index="/share">分享</el-menu-item>
  <el-menu-item index="/job">招聘</el-menu-item>
  <el-menu-item index="/dev">客户端测试</el-menu-item>
  <el-col :span="3" :offset="5">
    <span v-if="success ==false">
    <el-menu-item @click="dialogVisible =true" >
       登录
    </el-menu-item>
  </span>
  <span v-else>
     <el-submenu index="3">
       <template slot="title">{{a.loginname}}</template>
        <el-menu-item  @click="gerenzhongxintiaozhuang(a.loginname)">个人中心</el-menu-item>
        <el-menu-item @click="xiaoxitongzhi(a.loginname)">消息通知</el-menu-item>
        <el-menu-item @click="fabiaohuati(a.loginname)">发表话题</el-menu-item>
        <el-menu-item @click="tuichu">退出</el-menu-item>
     </el-submenu>
       </span>
       </el-col>
  </el-menu>
      
</el-row>
<!-- 下面内容 数据-->
    <el-row>
    <el-card >
        <router-view/>
     </el-card >
      <el-col :span="20"><p></p></el-col>
  </el-row>


<!-- 登录框 -->
<el-dialog
  title="登录"
  :visible.sync="dialogVisible"
  width="30%"
  >
  <el-input v-model="accesstoken" placeholder="Access Token" clearable ></el-input>
  <p></p>
    <el-button type="success" @click="denglu">登录</el-button>
</el-dialog>



  </div>
</template>

<script>

export default {
   name: 'App',
    data() {
      return {
        //弹出框是否弹出
        dialogVisible:false,
        //文本框
        accesstoken:'',
        a:'null',
        //是否登录
        success:false,
        //用户名
        loginname:'',
        
      };
     
    },
    //组件创建成功后的回调函数
    cerated:function(){
        var loginStatus=window.localStorage.getItem('loginStatus');
        if(loginStatus){
          this.denglu();
          this.xuigai(
            window.localStorage.getItem('loginname')
          )
        }
    },
    methods:{
      //点击loge返回首页
   tiaozuan3(){
        this.$router.push('/all')
    },
    //个人中心跳转
    gerenzhongxintiaozhuang(loginname){
        this.$router.push('/user/' + loginname)
    },
    //发表话题跳转
    fabiaohuati(loginname){
         this.$router.push('/fbht/' + loginname)
    },
    //消息通知跳转
    xiaoxitongzhi(loginname){
         this.$router.push('/xxtz/'+loginname)
    }
    ,
    //退出登录，并返回首页
    tuichu(){
      this.success=false;
      this.$router.push('/all')
    },
    //修改loginname
    xuigai(state,payload){
      state.loginname=payload;
    },
    //用户登录
    denglu(){
      var app=this;
      //关闭对话框
      app. dialogVisible=false,
      this.$http.post('/accesstoken',{
        accesstoken:this.accesstoken
      }).then(function (response) {
        if(response&&response.data&&response.data.success){
        app.a=response.data;
        app.success=response.data.success;
        //跳转个人中心
         app.$router.push('/user/'+response.data.loginname);
         //保存登录数据到localStorage
         window.localStorage.setItem
         ('loginStatus,ture');
         window.localStorage.setItem
         ('loginname',response.data.loginname)
        }
      })
    } ,  
}


  };

</script>

<style>

</style>



