<template>
    <div>
      <div v-if="topic">
      <el-row>
  <el-col :span="5" :offset="1">
    <el-card>
      <div>个人信息</div>
      <hr>
      <div>
        <span @click="tiaozuan2(topic.author.loginname)" class="xiaoshou">
          <img :src="topic.author.avatar_url" alt="" class="touxiang">
      {{topic.author.loginname}}</span>  
      </div>
      <br>
      积分：{{topic.reply_count}}
      <hr>
    </el-card>
    </el-col>
    <!-- 标题信息 -->
    <el-col :span="11" :offset="1">
      <h3>{{topic.title}}</h3>
      <span class="xinxi">
        • 发布于{{topic.create_at}}&nbsp;
        •作者 {{topic.author.loginname}}&nbsp;
        •{{topic.visit_count}} 次游览&nbsp;
        •来自{{topic.tab}} 
      </span>
    </el-col>
    
   <!-- 内容 -->
<el-col :span="14" :offset="1">
 <span v-html="topic.content" class="neirong"></span>
  </el-col>
  <!-- 回复数 -->
<el-col :span="14" :offset="7">
  <el-card>
    <span>{{topic.reply_count}} 回复</span>
 <!-- 下方评论 -->
 <div v-for="(item,index) in topic.replies" :key="index">
    <div>
      <span  @click="tiaozuan2(item.id)" class="xiaoshou"> 
        <img :src="item.author.avatar_url" alt="" class="touxiang ">
       </span>
        {{index+1}}楼</div>
 <span v-html="item.content"></span>
 </div>
 </el-card>
  </el-col>

</el-row>
</div>
    </div>
</template>
<script>
export default{
name:'topics',
props:['id'],
data:function(){
  return{topic:null,a:false};
},
methods:{
   tiaozuan2(loginname){
        this.$router.push('/user/'+loginname)
    },
},
beforeRouteEnter:function(to,from,next){
   next(function(vm){
     //ajax
      vm.$http.get('/topic/'+to.params.id).then(function(response){
        vm.topic=response.data.data;
      });
   });
},
beforeRouteUpdate:function(to,from,next){
  var app=this;
 this.$http.get('/topic/'+to.params.id).then(function(response){
        app.topic=response.data.data;
      });
}

}


</script>
<style >
.xiaoshou{
  cursor:pointer
}
.touxiang{
  width: 30px;
    height: 30px;  
}
.xinxi{
      font-size: 12px;
    color: #838383;
}
.mingzi{
  font-size: 12px;
    font-weight: 700;
    color: #666;
    text-decoration: none;
}
.neirong img{
  height: auto;
    max-width: 100%;
    vertical-align: middle;
    border: 0;
}
.neirong{
  white-space: pre-wrap;
}

</style>


