<template>
    <div>
      <el-row>
  <el-col :span="5" :offset="1">
    <el-card>
      <div>个人信息</div>
      <hr>
      <div>
        <span >
            <!-- 头像 -->
         <img :src="user.avatar_url" alt="" class="touxiang">
         <!-- 名字 -->
         &nbsp;
         <span class="mingzi">
       {{user.loginname}} 
        </span>
      </span>  
      </div>
      <br>
      积分：
      {{user.score}}
    </el-card>
    </el-col>
   
        <el-col :span="15" :offset="1">
             <el-card>
            <div>主页</div>
            <br>
         <img :src="user.avatar_url" alt="" class="touxiang">
         &nbsp;
         <span class="mingzi">{{user.loginname}} </span>
         <br>
             积分：
      {{user.score}}
         <br>
         <span>
             注册时间：{{user.create_at}}
         </span>
         </el-card>
    <br>
    <el-card>
        <div>最近创建的话题</div>
        <br>
        <span v-for="item in user.recent_topics" :key="item">
            <el-col :span="24" >
                    <img :src="item.author.avatar_url" alt="" class="touxiang2">
           </el-col>
           <el-col :span="14" >
           <span class="neirong" @click="tiaozuan1(item.id)">
                {{item.title}}
             </span>
           </el-col>
                 <el-col :span="10" >
                {{item.last_reply_at}}
                 </el-col>
        </span>
    </el-card>
    <br>
     <el-card>
        <div>最近参与的话题</div>
        <br>
        <span v-for="item in user.recent_replies" :key="item">
            <el-col :span="24" >
                    <img :src="item.author.avatar_url" alt="" class="touxiang2">
           </el-col>
           <el-col :span="14" >
           <span class="neirong" @click="tiaozuan1(item.id)">
                {{item.title}}
             </span>
           </el-col>
                 <el-col :span="10" >
                {{item.last_reply_at}}
                 </el-col>
        </span>
    </el-card>
    </el-col>
    </el-row>
    </div>
</template>
<script>
export default{
name:'user',
props:['loginname'],
data:function(){
    return {
        user:''
    }
},
methods:{
    //点击标题进入内容
    tiaozuan1(id){
        this.$router.push('/topics/'+id)
    },
},
beforeRouteEnter:function(to,from,next){
   next(function(vm){
       vm.$http.get('/user/' +
       to.params.loginname).then(function(response){
            vm.user=response.data.data
       })
   })
},
beforeRouteUpdate:function(to,from,next){
  var app=this;
    //从新发送ajax请求
    this.$http.get('/user/'+
    to.params.loginname).then(function(response){
        app.user=response.data.data
    });
    next();
}

}

</script>
<style >
.touxiang{
  width:50px;
    height: 50px;  
}
.mingzi{
    position: absolute;
    top:80px;
}
.neirong{
     cursor:pointer;
    max-width: 70%;
    white-space: nowrap;
    display: inline-block;
    vertical-align: middle;
    font-size: 16px;
    line-height: 30px;
}
.touxiang2{
    width:30px;
    height:30px;  
}
</style>


